import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router)

const Home = () => import(/* webpackChunkName: "home" */ '@/pages/index');
const About = () => import(/* webpackChunkName: "about" */ '@/pages/about')
const Source = () => import(/* webpackChunkName: "source" */ '@/pages/source')
const DataAnalysis = () => import(/* webpackChunkName: "data-analysis" */ '@/pages/data-analysis')
export default new Router({
  routes: [
    { 
      path: '/', 
      name: 'home', 
      component: Home ,
      children: [
        {
          path: '/data-analysis', name: 'dataAnalysis', component: DataAnalysis
        }
      ]
    },
    { path: '/about', name: 'about', component: About },
    { path: '/source', name: 'Source', component: Source },
  ]
})